body {
  background: #f05a61;
  color: white;
  font-size: 20px;
  text-align: center;
  font-family: 'Righteous';
}

#container {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.lengths {
  display: block;
  width: 500px;
}

/* .flex {
  display: block;
} */

.timer-container {
  display: inline-block;
  width: 250px;
}

body #container .timer {
  display: block;
  border: 7px solid #13353a;
  margin: 20px auto 10px auto;
  width: 270px;
  height: 160px;
  border-radius: 50px;
  position: relative;
}

.main-title {
  font-size: 50px;
  margin-bottom: 20px;
}

.length-control {
  display: inline-block;
  width: 250px;
}

.btn-level {
  width: 40px;
  display: inline-block;
  vertical-align: middle;
}

.fas {
  font-size: 2em;
}


.time-length {
  font-family: digital;
  font-size: 50px;
  vertical-align: middle;
}

#time-left {
  font-family: digital;
  font-size: 80px;
}

@media screen and (max-width: 500px) {
  #container {
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    -ms-transform: scale(0.8);
    -o-transform: scale(0.8);
  }
}